<template>
  <div class="dashboard">
    <h1 class="subtitle-1 grey--text">Dashboard</h1>
      <v-container>
        <v-row
        class="ma-3"
        row wrap>
          <v-col cols="4">
            <v-sheet class="justify-center ma-3"
                     color="white"
                     elevation="5"
                     rounded
                     >
               <apexchart type="bar"
                          :options="optionsTotalVaccinations" 
                          :series="seriesTotalVaccinations" />
            </v-sheet>
            <v-sheet class="justify-center ma-3"
                     color="white"
                     elevation="5"
                     rounded
                     >
              <apexchart type="bar" 
                         :options="optionsAllProducer"
                         :series="seriesAllProducer" />
            </v-sheet>
          </v-col>
          <v-col cols="8">
            <v-sheet class="justify-center ma-3"
                     color="white"
                     elevation="5"
                     rounded
                     >
              <apexchart type="area"
                         :options="optionsFirstAndSecondVaccination"
                         :series="seriesFirstAndSecondVaccination"/>
            </v-sheet>
          </v-col>
        </v-row>
      </v-container>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Dashboard',
  components: {
  },
  data:() => ({

    storedData: {
      oneDoseVaccine: [],
      twoDoseVaccine: [],
      BiontechData: [],
      ModernaData: [],
      ZenecaData: [],
      firstVaccination: [],
      secondVaccination: [],
      allDates: []
    }
  }),
  methods: {
    async getVaccinationDataHistory() {
    const urlForVaccinations = 'https://api.corona-zahlen.org/vaccinations'
    const urlForVaccinationsHistory = 'https://api.corona-zahlen.org/vaccinations/history'
    // const urlForHisotryCases = 'https://api.corona-zahlen.org/germany/history/cases'

    const response = await axios.get(urlForVaccinations)
      this.entryVaccinationData = response.data.data
      this.storedData.oneDoseVaccine = this.entryVaccinationData.vaccinated
      this.storedData.twoDoseVaccine = this.entryVaccinationData.administeredVaccinations
      this.storedData.AllProducer = this.entryVaccinationData.vaccination
      this.storedData.BiontechData = this.entryVaccinationData.vaccination.biontech
      this.storedData.ModernaData = this.entryVaccinationData.vaccination.moderna
      this.storedData.ZenecaData = this.entryVaccinationData.vaccination.astraZeneca

    const response2 = await axios.get(urlForVaccinationsHistory)
      this.historyDates = response2.data.data.history
      this.storedData.firstVaccination = this.historyDates.map( v => v.firstVaccination)
      this.storedData.secondVaccination = this.historyDates.map (v2 => v2.secondVaccination)
      this.storedData.allDates = this.historyDates.map( a => a.date)
      },
  },
  computed: {
    // Overall Vaccinations 1 & 2 Vaccination
    seriesTotalVaccinations(){
      return [{
        name: 'Impfungen',
        data: [this.storedData.oneDoseVaccine, this.storedData.twoDoseVaccine]
      }]
    },
    optionsTotalVaccinations(){
      return {
        chart: {
              toolbar: false,
              type: 'bar',
              height: 350
            },
            plotOptions: {
              bar: {
                horizontal: false,
              }
            },
            dataLabels: {
              enabled: true
            },
            xaxis: {
              categories: [ 'Erste Imfpung', 'Erste und Zweite Imfpung'],
            },
            yaxis: {
              show: false
            },
            tooltip: {
              enabled: true
            },
            x: {
              show: true
            },
            grid: {
              show: false
            },            
        }
    },
    // Vaccin producer
    seriesAllProducer(){
      return [{
        name: 'Verabreichte Dosen',
        data: [this.storedData.BiontechData, this.storedData.ModernaData, this.storedData.ZenecaData]
      }]

    },
    optionsAllProducer(){
      return {
        chart: {
              toolbar: false,
              type: 'bar',
              height: 350
            },
            plotOptions: {
              bar: {
                horizontal: false,
              }
            },
            dataLabels: {
              enabled: true
            },
            xaxis: {
              categories: ['Biontech', 'Moderna', 'Astra Zeneca'],
            },
            yaxis: {
              show: false
            },
            tooltip: {
              enabled: true
            },
            grid: {
              show: false
            },
      }
    },
    // First and Second Vaccinations compaired everyday
    seriesFirstAndSecondVaccination(){
      return [{
          name: 'erste Impfung',
          data: this.storedData.firstVaccination
        },
        {
          name: 'zweite Impfung',
          data: this.storedData.secondVaccination
      }]
    },
    optionsFirstAndSecondVaccination(){
          return {
        chart: {
            type: 'area',
            toolbar: {
                show: false
            }
        },
        dataLabels: {
            enabled: false,
        },
        stroke: {
            curve: 'smooth'
        },
        theme: {
            mode: 'light'
        },
        grid: {
            row: {
                colors: ['#cccccc', 'transparent'],
                opacity: 0.5
            }
        },
        xaxis: {
          type: 'datetime',
          categories: this.storedData.allDates
        },
        yaxis: {
          show: false
        }
    };
    },
  },
  mounted(){
    this.getVaccinationDataHistory()

  }
}
</script>