<script>
import * as d3 from 'd3'
import * as topojson from 'topojson'
import VueKofi from 'vue-kofi'


export default {
  name: 'Home',
  data: () => ({
  }),
  
  components: {
    VueKofi
  },
  methods: {
    d3Fetch(){
      const topoUrlGermany = 'https://raw.githubusercontent.com/deldersveld/topojson/master/countries/germany/germany-regions.json'
      d3.json(topoUrlGermany)
        .then((data, error) => {
          if(error){
            console.log(error);
          }else{
              this.countryData = data
              this.countryData = topojson.feature(data, data.objects.DEU_adm2)
              console.log(this.countryData);
              this.drawMap()

          }
        })
    },
    drawMap(){
      let w = 500, 
          h = 500;

      let svg = d3.select('#container').append('svg').attr("preserveAspectRatio", "xMinYMin meet").attr('viewBox', [0, 0, w, h]).classed("svg-content", true);
      let projection = d3.geoMercator().translate([w/2, h/2]).scale(2100).center([10.02, 51.02]);
      let path = d3.geoPath().projection(projection)

      svg.selectAll('path') 
        .data(this.countryData.features)
        .enter()
        .append('path')
        .attr('class', 'country')
        .attr('d', path)
        .attr('fill', '#3cd1c2')
        // .attr('fill', (countryDataItems) => {
        //   let id = countryDataItems['ID_1']
        //   let county = this.entryVaccinState.find((item) => {
        //      return item['name'] === id
        //   })
        //   let sortedData = county['vaccinated']
        //   if(sortedData <= 1000){
        //     return 'tomato'
        //   }
        // })
    },
    
      // const UrlForCStateVaccin = 'https://api.corona-zahlen.org/vaccinations'
      
  },
  computed: {},
  mounted(){
    this.d3Fetch()
   }
}
</script>

